body {
    font-family: Arial, sans-serif; /* Font for the site */
    margin: 0;
    padding: 0;
}

/* Container for the home component */
.home {
    padding: 20px;
    text-align: center; /* Center align content */
}

/* Headline for typing effect */
h1 {
    font-size: 2.5em; /* Large font for headline */
    margin: 20px 0;
}

/* Staggered letter animation */
.letter {
    display: inline-block;
    opacity: 0; /* Start hidden */
    animation: fadeIn 0.5s forwards; /* Fade-in effect */
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

/* Paragraph styling */
.fade-in {
    font-size: 1.2em; /* Medium font for description */
    color:white;
    margin: 10px 0;
}

/* Social media links styling */
.social-media {
    margin: 20px 0;
}

.social-icons {
    list-style-type: none; /* No bullet points */
    padding: 0;
}

.social-icons li {
    display: inline; /* Display items inline */
    margin: 0 15px; /* Spacing between icons */
}

/* Button styles */
.btn {
    background-color: #007BFF; /* Button color */
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1em;
    transition: background-color 0.3s ease; /* Smooth transition */
}

.btn:hover {
    background-color: #0056b3; /* Darker shade on hover */
}

/* Responsive Styles */
@media (max-width: 768px) {
    h1 {
        font-size: 2em; /* Reduce headline size on mobile */
    }

    .fade-in {
        font-size: 1em; /* Smaller text on mobile */
    }

    .btn {
        width: 100%; /* Full-width button on mobile */
        padding: 15px; /* Increased padding for larger touch target */
    }
}