header {
    position: relative;
    min-height: 500px;
    width: 100%;
    background: yellowgreen url(../components/image/image.png) no-repeat top center;
    background-size: cover;
    text-align: center;
    padding: 50px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

header .banner {
    margin: 0 auto;
    width: 100%;
    max-width: 800px;
    text-align: center;
}

header .banner-text h1 {
    font: 48px 'opensans-bold', sans-serif;
    color: #fff;
    margin-bottom: 18px;
    text-shadow: 0px 1px 3px rgba(0, 0, 0, .8);
}

header .banner-text h3 {
    font: 18px 'librebaskerville-regular', serif;
    color: #A8A8A8;
    text-shadow: 0px 1px 2px rgba(0, 0, 0, .5);
}

header .social {
    margin: 24px 0;
    font-size: 30px;
}

header .social li {
    display: inline-block;
    margin: 0 15px;
}

header .scrolldown a {
    color: #fff;
    font-size: 42px;
}

@media (min-width: 768px) {
    header .banner-text h1 {
        font-size: 64px;
    }
}

/* Reset default margins, paddings, borders, and outlines */
nav-wrap ul, #nav-wrap li, #nav-wrap a {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
}

/* Navigation wrapper style */
#nav-wrap {
    font: 12px 'Open Sans', sans-serif; /* Set font properties */
    background-color: #333; /* Background color of the navbar */
    width: 100%; /* Full width of the viewport */
    text-transform: uppercase; /* Uppercase letters for menu items */
    letter-spacing: 2.5px; /* Spacing between letters */
    margin: 0 auto; /* Center the navbar */
    z-index: 100; /* Ensure the navbar is above other elements */
    position: fixed; /* Fixed positioning at the top */
    left: 0; 
    top: 0; 
}

/* Class for opaque backgrounds for potentially scrolling headers */
.opaque { 
    background-color: #333; 
}

/* Hide toggle button for mobile navigation if applicable */
#nav-wrap > a.mobile-btn { 
    display: none; 
}

/* Navigation list properties */
ul#nav {
    min-height: 48px; /* Minimum height of the nav */
    width: auto; /* Automatic width */
    text-align: center; /* Center the menu items */
}

ul#nav li {
    position: relative; /* Relative positioning for the list items */
    list-style: none; /* Remove bullet points */
    height: 48px; /* Height of the list items */
    display: inline-block; /* Inline display for horizontal layout */
}

/* Links within navigation */
ul#nav li a {
    display: inline-block; /* Inline block for clickability */
    padding: 8px 13px; /* Padding for spacing within the links */
    line-height: 32px; /* Line height for vertical alignment */
    text-decoration: none; /* Remove underline from links */
    text-align: left; /* Align text to the left */
    color: #fff; /* White text color */

    /* Transition effects for smoother hover effects */
    transition: color .2s ease-in-out; 
}

/* Active link style */
ul#nav li a:active { 
    background-color: transparent !important; /* Ensure background remains transparent when active */
}

/* Current link style */
ul#nav li.current a { 
    color: white; /* White color for the current menu item */
}

@media (max-width: 600px) {
    #nav-wrap > a.mobile-btn {
        display: block; /* Show toggle button */
    }

    ul#nav {
        display: none; /* Initially hide the nav links */
        flex-direction: column; /* Stack items vertically */
        width: 100%; /* Full width on mobile */
    }

    ul#nav.open {
        display: flex; /* Display nav when open */
    }

    ul#nav li {
        height: auto; /* Allow for flexible heights */
        padding: 10px 0; /* Add some padding */
    }
}