#about {
    position: relative;
    width: 100%;
    min-height: 500px;
    text-align: center;
    background: #161415 url('./image/image.png') no-repeat top center;
    background-size: cover;
    padding: 50px 20px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
}

.profile-pic {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 20px;
}

.about-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.contact-details {
    width: 90%;
    max-width: 600px;
    padding: 20px;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 10px;
    margin-top: 30px;
    color: white;
}

#about h2 {
    font-size: 24px;
    color: white;
    margin-bottom: 15px;
}

#about p, #about li {
    font-family: 'Times New Roman', serif;
    font-size: 18px;
    line-height: 1.6;
    color: white;
    text-align: center;
    list-style: none;
}

@media (min-width: 768px) {
    #about {
        flex-direction: row;
        justify-content: center;
        align-items: flex-start;
        padding: 100px 50px;
    }

    .profile-pic {
        margin: 0 40px 0 0;
    }

    .about-content {
        align-items: flex-start;
        text-align: left;
        max-width: 600px;
    }

    .contact-details {
        margin-top: 20px;
        text-align: left;
    }
}